let yearInput = document.getElementById('yearInput');
let btn = document.getElementById('checkBtn');
let res = document.getElementById('res');

function checkYear() {
  let year = yearInput.value.trim();

  res.className = '';

  if (year === '') {
    res.innerText = '–í–≤–µ–¥—ñ—Ç—å —Ä—ñ–∫!';
    res.classList.add('error');
    return;
  }

  year = parseInt(year);

  if (isNaN(year)) {
    res.innerText = '–¶–µ –Ω–µ —á–∏—Å–ª–æ';
    res.classList.add('error');
  } else {
    if ((year % 4 === 0 && year % 100 !== 0) || year % 400 === 0) {
      res.innerText = '–í–∏ –Ω–∞—Ä–æ–¥–∏–ª–∏—Å—è —É –≤–∏—Å–æ–∫–æ—Å–Ω–∏–π —Ä—ñ–∫!';
      res.classList.add('success');
    } else {
      res.innerText = '–¶–µ –Ω–µ –≤–∏—Å–æ–∫–æ—Å–Ω–∏–π —Ä—ñ–∫.';
      res.classList.add('error');
    }
  }
}

btn.addEventListener('click', checkYear);
